import React from 'react';
import { HashRouter as Router, Redirect, Route,Switch } from 'react-router-dom'
import Shouye from './Shouye'
import Gouwuche from './Mygouwuche'
import Dingdan from './Dingdan'
import Mytaobao from './Mytaobao'
import Search from './Search'
import Shangpin from './Shangpin'
import { Provider } from 'react-redux'
import store from './store'
import Login from './Login';
const App = () => {
    return <Provider store={store}>
    <Router>
        <Switch>
            <Route exact path='/' component={Shouye} />
            <Route path='/gouwuche' component={Gouwuche} />
            <Route path='/dingdan' component={Dingdan} />
            <Route path='/mytaobao' component={Mytaobao} />
            <Route path='/search' component={Search} />
            <Route path='/shangpin' component={Shangpin}/>
            <Route path='/login' component={Login}/>
            <Route render={()=><Redirect to='/'/>}/>
        </Switch>
    </Router>
    </Provider>
}
export default App;