
const shoucangreducer = (state = [], action) => {
    if (action.type == 'ADD_SHOUC') {
        return [...state,action.value]
    }
    if (action.type == 'DEL_SHOUC') {
        let arr
        arr = [...state].filter((item) => {
            // console.log(item)
            // console.log(action)
            return item != action.index
        })
        // console.log(arr)
        return arr
    }
    return state
}

export default shoucangreducer

