import React from 'react'
import {
    withRouter
} from 'react-router-dom'
import Mytab from './Mytab';
import { connect } from 'react-redux'
import { login } from './actionCreator.js'
const Loginr = (props) => {
    const log = () => {//跳转到登陆界面就是没得登录我直接请求用户信息之后goBack()
    //在收藏页面goBack并不会回到收藏直接返回首页在商品的详情页是符合预期的(放弃)
        props.dispatch(login())
        props.history.goBack()
    }
    return (
        <div className='login'>
            <button
                onClick={log}
            >登录</button>
        </div>
    )
}

const mapStateToProps = (state) => ({
    username: state.login.username
})
export default withRouter(connect(mapStateToProps)(Loginr));