import React from 'react'
import {
  withRouter
} from 'react-router-dom'
import { SearchBar } from 'antd-mobile';
import './css/search.css'
const Search = (props) => {
  return (
    <div className='sreach'>
      <SearchBar
        showCancelButton
        disabled
        onCancel={() => props.history.goBack()}
      />
    </div>
  )
}
export default withRouter(Search);