import React from 'react'
import 'antd-mobile/dist/antd-mobile.css'
import { TabBar } from "antd-mobile"
import { withRouter } from "react-router-dom"
import { connect } from 'react-redux'
import { get } from 'jquery'
class Tabsp extends React.Component {//状态的读取？？本地存储nb
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '',
            status: true
        };
        console.log(props)
        props.shoucang.map((item) => {
            if (item == this.props.location.state) {
                this.state = {
                    selectedTab: 'shoucang',
                    status: false
                };
            }
            console.log(this.state.status)
        })
    }

    let = () => {


        if (this.state.status == true) {
            this.setState({
                selectedTab: 'shoucang',
                status: false
            });
            this.props.dispatch({
                type: 'ADD_SHOUC',
                value: this.props.location.state,//id
            })
        } else {
            this.setState({
                selectedTab: 'shoucang',
                status: true
            });
            this.props.dispatch({
                type: 'DEL_SHOUC',
                index: this.props.location.state
            })

        }
    }
    log = (props) => {
        props.history.push('/login')
    }
    render() {
        return (
            <div style={{ position: 'fixed', height: '70px', width: '100%', bottom: 0 }}>
                <TabBar
                    unselectedTintColor=""
                    tintColor="#5D656B"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>
                        }

                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        title="客服"
                        key="kefu"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>
                        }
                        selected={this.state.selectedTab === 'shoucang' && this.state.status == false}
                        onPress={() => {//这里可以扩展一下收藏过的商品点进去是选中的状态
                            this.props.username
                                ? this.let(this.props)
                                : this.log(this.props)
                        }}
                        title="收藏"
                        key="shoucang"
                    ></TabBar.Item>
                    <TabBar.Item
                        title="加入购物车"
                        key="jiaru"
                    ></TabBar.Item>
                    <TabBar.Item
                        title="立即购买"
                        key="liji"
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        username: state.login.username,
        shoucang: state.shoucang
    }
}
export default withRouter(connect(mapStateToProps)(Tabsp))