import { Grid } from 'antd-mobile';
import React, { Component } from 'react'
// import './css/mygrid.css'
import './css/listv.css'
import {
  withRouter
} from 'react-router-dom'
import { connect } from 'react-redux'
// import { getlist } from './actionCreator.js'
class ListV extends Component {
  get = (numlist) => {
    let arr = []
    for (let i = 0; i < numlist.length; i++) {
      arr.push(this.props.datalist.filter((item) => {
        return numlist[i] == item.id
      })[0])
    }
    // console.log(this.props)
    return arr
  }
  del = (id) => {
    this.props.dispatch({
      type: 'DEL_SHOUC',
      index: id
    })
  }
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='listv'>
        <Grid
          data={this.get(this.props.shoucang)}
          columnNum={1}
          itemStyle={{ height: '120px', margin: '9px 4.5px 4.5px 4.5px', background: 'rgba(0,0,0,.05)' }}
          // onClick={dataItem => {
          //   this.props.history.push({ pathname: "/shangpin", state: dataItem.id })
          // }}
          hasLine={false}
          renderItem={dataItem => (
            <div className='listitem'>
              <img className='listitem-img' style={{ border: 'node', }} src={dataItem.img} alt="" />
              <div className='listitem-div'>
                <span className='listitem-span-one'>{dataItem.title}</span><br />
                <span className='listitem-span-four'>￥</span>
                <span className='listitem-span-two'>{dataItem.price}</span>
                <div className='listitem-span-three'>
                  <button className='listitem-btn-one'>购买</button>
                  <button className='listitem-btn-two'
                    onClick={() => {
                      console.log(dataItem.id)
                      this.del(dataItem.id)
                    }}
                  >删除</button>
                </div>
              </div>
            </div>
          )
          }
        />
        <div className='dataitem-bottom'></div>
      </div>
    )
  }
};
const mapStateToProps = (state) => ({
  shoucang: state.shoucang,
  datalist: state.datalist
})
export default withRouter(connect(mapStateToProps)(ListV));