import { Carousel, WingBlank } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import React from 'react'
class Mycarousel extends React.Component {
  state = {
    data: [],
    imgHeight: 120,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['https://picasso.alicdn.com/imgextra/i2/382208/O1CNA11UbqJH100ff4a26c580b0b7653000k_!!382208-0-picassobanner.jpg',
         'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 
         'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg', 
         'https://img.alicdn.com/imgextra/i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban.jpg', 
         'https://gw.alicdn.com/imgextra/i2/161/O1CN01YdcfUK1D3mOfsf8TP_!!161-0-lubanu.jpg', 
         'https://gw.alicdn.com/imgextra/i4/130/O1CN01rl6AUW1Cpa7QeEvPv_!!130-0-ppp-picassogw.jpg', 
         'https://img.alicdn.com/imgextra/i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban.jpg',
         'https://img.alicdn.com/imgextra/i2/14/O1CN01FQBQC01BySAuFMDxB_!!14-0-luban.jpg'
         ],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default withRouter(Mycarousel);