import React from 'react'
import {
    withRouter
} from 'react-router-dom'
import './css/shangpin.css'
import Tabsp from './Tabsp';
import { connect } from 'react-redux'
const Shangpin = (props) => {
    //商品详情页刷新问题     在商品详情页面刷新的时候图片信息会丢失
    //现在这玩意倒是不会消失了，只是收藏的是假的，无法显示
    //通过本地存储完美解决
    let storage = window.localStorage;
    let info = { img: '' }
    props.datalist.forEach((item) => {
        // console.log(props.location.state)
        if (item.id == props.location.state) {
            info = item;
        }
    })
    if (info.img == '') {
        info.img = storage.getItem('img')
        console.log(props.location.state)
        props.location.state = storage.getItem('id',info.id)
    } else {
        storage.setItem('img', info.img)
        // console.log(props.location.state)
        storage.setItem('id',info.id)
    }

    return (
        <div className='shangpin'>
            <div className='shangpin-image'>
                <img src={info.img} alt=""></img>
                <span className='shangpin-button-one' onClick={() => { props.history.push('/') }}><i className='iconfont icon-fanhui' /></span>
                <Tabsp />
            </div>
        </div>
    )
}
const mapStateToProps = (state) => ({
    datalist: state.datalist
})
export default withRouter(connect(mapStateToProps)(Shangpin));