import { Grid } from 'antd-mobile';
import React, { Component } from 'react'
import './css/mygrid.css'
import {
  withRouter
} from 'react-router-dom'
import { connect } from 'react-redux'
import { getlist } from './actionCreator.js'
class Mygrid extends Component {
  constructor(props) {
    super(props);
    props.dispatch(getlist())
  }
  render() {
    return (
      <div className='mygrid'>
        <div className='mygridimg'><img src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png' alt='' /></div>
        <Grid
          data={this.props.datalist}
          columnNum={2}
          itemStyle={{ height: '260px', width: '171px', margin: '9px 4.5px 0px 4.5px', background: 'rgba(0,0,0,.05)' }}
          onClick={dataItem => {
            this.props.history.push({ pathname: "/shangpin", state: dataItem.id })
          }}
          hasLine={false}
          renderItem={dataItem => (
            <div className='dataitem'>
              <img className='dataitem-img' style={{ border: 'node', }} src={dataItem.img} alt="" />
              <div className='dataitem-div-one'>{dataItem.title}</div>
              <div className='dataitm-div-two'>
                <span className='dataitem-span-one'>￥</span>
                <span className='dataitem-span-two'>{dataItem.price}</span>
                <span className='dataitem-span-three'>{dataItem.id}已购买</span>
              </div>
            </div>
          )
          }
        />
        <div className='dataitem-bottom'></div>
      </div>
    )
  }
};
const mapStateToProps = (state) => ({
  datalist: state.datalist
})
export default withRouter(connect(mapStateToProps)(Mygrid));