import React from 'react'
import 'antd-mobile/dist/antd-mobile.css'
import { TabBar } from "antd-mobile"
import { withRouter } from "react-router-dom"

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{ position: 'fixed', height: '70px', width: '100%', bottom: 0 }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#5D656B"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="shouye"
                        icon={
                            <i className='iconfont icon-shouye1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>
                        }
                        selected={pathname=== '/'}
                        onPress={() => {
                            this.props.history.push('/')
                        }}
                        data-seed="logId"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-htmal5icon29'></i>
                        }
                        title="收藏"
                        key="gouwuche"
                        selected={pathname === '/gouwuche'}
                        onPress={() => {
                            this.props.history.push('/gouwuche')
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao1' ></i>
                        }
                        title="订单"
                        key="dingdan"
                        selected={pathname === '/dingdan'}
                        onPress={() => {
                            this.props.history.push('dingdan')
                        }}
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-wodedangxuan'></i>
                        }
                        title="我的淘宝"
                        key="mytaobao"
                        selected={pathname === '/mytaobao'}
                        onPress={() => {
                            this.props.history.push('mytaobao')
                        }}
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter(Mytab)
